//import searchbox from "./components/Searchbox";
import "./App.css";
import JSONDATA from "./records.json";
import * as React from "react";
//import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import "bootstrap/dist/css/bootstrap.css";
//import SearchBar from "material-ui-search-bar";
import { useState } from "react";
import ReturnModal from "react-modal";
import BookModal from "react-modal";
import Modal from "react-modal";
import DatePicker from "react-datepicker";

import "react-datepicker/dist/react-datepicker.css";
<link
  rel="stylesheet"
  href="https://cdn.jsdelivr.net/npm/bootstrap@5.1.3/dist/css/bootstrap.min.css"
  integrity="sha384-1BmE4kWBq78iYhFldvKuhfTAU6auU8tT94WrHftjDbrCEXSU1oBoqyl2QvZ6jIW3"
  crossorigin="anonymous"
/>;
Modal.setAppElement('#root')
function App() {
  const [searchTerm, setSearchTerm] = useState("");
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [returnModalIsOpen, setReturnModalIsOpen] = useState(false);

  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());

  const [value, setValue] = React.useState({});
  return (
    <div className="container">
      <div className="App">
       
        <input
          type="text"
          placeholder="Search.."
          onChange={(event) => {
            setSearchTerm(event.target.value);
          }}
        />

        {/* <searchbox/> */}
       
      

        <br></br>
        <br></br>
        {/* <div className="user" key={ key }>  */}
        <TableContainer component={Paper}>
        <table sx={{ minWidth: 650 }} aria-label="simple table" >
          <TableHead sx={{backgroundColor:'#061094'}}>
            <TableRow>
              <TableCell>Name</TableCell>
              <TableCell align="right" color="white">Code</TableCell>
              <TableCell align="right">availability</TableCell>
              <TableCell align="right">Needing Repair</TableCell>
              <TableCell align="right">Durability</TableCell>
              <TableCell align="right">Mileage</TableCell>
            </TableRow>
          </TableHead>

          {JSONDATA.filter((val)=> {
            if (searchTerm === "") {
              return val;
            } else if (
              val.name.toLowerCase().includes(searchTerm.toLowerCase())
            ) {
              return val;
            }
          }).map((val) => {
            return (
              <TableBody>
                <TableRow
                  key={val.name}
                  sx={{ "&:last-child td, &:last-child th": { border: 0 },  }}
                >
                  <TableCell component="th" scope="row">
                    {val.name}
                  </TableCell>
                  <TableCell align="right">{val.code}</TableCell>
                  <TableCell align="right">
                    {val.availability.toString()}
                  </TableCell>
                  <TableCell align="right">
                    {val.needing_repair.toString()}
                  </TableCell>
                  <TableCell align="right">{val.durability}</TableCell>
                  <TableCell align="right">
                    {val.mileage ? val.mileage : "N/A"}
                  </TableCell>
                </TableRow>
              </TableBody>
              
            )
          })
          
          }
        </table>
        </TableContainer>
        <br></br>

        {/* start modal */}
        {/* <!-- Modal toggle --> */}

        
{/* Modal of book */}
        <button onClick={ ()=> setModalIsOpen(true)} style={
          {
          overlay: {
            backgroundColor:'orange'
          },
          content: {
            color: 'Blue'
          }
        }
      }>Book</button>
        <BookModal isOpen={modalIsOpen} shouldCloseOnOverlayClick={false} onRequestClose= { ()=> setModalIsOpen(false)}
        style={
          {
          overlay: {
            backgroundColor:'grey'
          },
          content: {
            color: 'Blue'
          }
        }
      }
        >
          <h1>Book a Product</h1>
          <p>Product booking</p>

          <DatePicker selected={startDate} onChange={(date) => setStartDate(date)} />
          <DatePicker selected={endDate} onChange={(date) => setEndDate(date)} />
         
          
          {/* //calculate time difference   */}
         {/* var time_difference = startDate.getTime() - endDate.getTime();  */}
  
         {/* //calculate days difference by dividing total milliseconds in a day   */}
         {/* var days_difference = time_difference / (1000 * 60 * 60 * 24);   */}
         
          {/* <p>you selected {startDate} </p> */}

          <select name="choice">
  <option value="first">First Value</option>
  <option value="second" selected>Second Value</option>
  <option value="third">Third Value</option>
</select>
      <div>
          <a href='#'>No</a> 
          <a href='#'>Yes</a>
      </div>
          <div>
          <button>Yes</button>
            <button onClick={ ()=> setModalIsOpen(false)}>No</button>
          </div>
        </BookModal>
        {/* modal 2 */}
        {/* Modal of return */}
        <button onClick={ ()=> setReturnModalIsOpen(true)}>Return</button>

        <ReturnModal isOpen={returnModalIsOpen} shouldCloseOnOverlayClick={false} onRequestClose= { ()=> setReturnModalIsOpen(false)}
        style={
          {
          overlay: {
            backgroundColor:'grey'
          },
          content: {
            color: 'Black'
          }
        }
      }
        >
          <h1>Return a Product</h1>
          <p>Product return</p>
          <div>
            <button style={{color:'white'}} onClick={ ()=> setReturnModalIsOpen(false)}>Close</button>
          </div>
        </ReturnModal>

        {/* end modal */}
      </div>
      
    </div>
  )
}

export default App
